// these are for nav.js to get our sass color vars and change onscroll
 :root {
  --desktop-nav-bg-var: #{$desktop-nav-bg};
  --mobile-nav-bg-var: #{$black-color}; }

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  white-space: nowrap;
  position: fixed;
  top: 0;
  width: 100%;
  height: 150px;
  color: $white-color;
  background-color: rgba(0,0,0,0.1);
  font-weight: $heavy-font;
  // transition for onscroll background color change
  transition: 0.7s ease-in-out;
  z-index: 9999; }

.nav-links {
  li {
    padding: 10px 10px;
    border-radius: 3px;
    transition: 0.3s ease-in-out; } }

.nav-brand {
  padding-left: $container-margin-x;
 }  //transform: scale(1.2)

.logo {
  //display: none !important
  transition: 0.7s ease-in-out; }

.nav-img {
  transition: 0.7s ease-in-out;
 }  //height: 120px

.nav-brand:hover {}

.nav-links {
  padding-right: $container-margin-x;
  display: flex;
  font-size: 1.125rem;
  text-decoration: none;
  list-style: none;
  a {
    // we need a better way of spacing out the links
    margin-left: 2rem;
    // transition for when we hover over a link and fade out the others
    transition: 0.2s ease-in-out; } }
.burger {
  display: none; }

@media screen and ( min-width : 1024px ) {
  // assume touch so dont bother looding these
  .nav-links:hover > a {
    opacity: 0.5; }

  .nav-links:hover > a:hover {
    opacity: 1;
    li {
      background-color: $vivid-red-color; } } }

@media screen and ( max-width : 768px ) {
  nav {
    height: 10vh;
    background-color: rgba(0,0,0,1); }

  .logo-text {
    display: none; }
  .logo {
    height: 10vh; }

  .nav-links {
    position: absolute;
    right: 0;
    top: 0;
    margin: 0;
    padding: 0;
    width: 95vw;
    height: 80vh;
    flex-direction: column;
    align-self: baseline;
    border-bottom-left-radius: 40px;
    padding: 20vh 0 0 5vw;
    background-color: $black-color;
    color: $white-color;
    z-index: -1;
    // transition for the whole slider
    transform: translateX(100%);
    transition: transform 0.5s ease-in-out; }

  .nav-active {
    // we toggle this class in mobile-nav-slider.js on burger click
    transform: translateX(0%); }

  .nav-links li {
    opacity: 0;
    vertical-align: top;
    padding-bottom: 1vh;
    margin-bottom: 6vh;
    border-bottom: 1px solid rgb(130, 130, 130);
    a {
      margin: 0;
      padding: 0; } }

  .burger {
    padding-right: $container-margin-x;
    display: block;
    cursor: pointer; }

  .burger-line-1, .burger-line-2, .burger-line-3 {
    background-color: $white-color;
    width: 25px;
    height: 1px;
    margin: 7px; }

  .burger-line-1 {
    transition: all 0.6s ease-in-out; }

  .burger-line-2 {
    transition: all 0.3s ease-in-out;
    // delay on closing
    transition-delay: 0.3s; }

  .toggle {
    .burger-line-1 {
      width: 8px;
 } }      // reduce by 17 px

  .toggle {
    .burger-line-2 {
      width: 17px;
      // reduce by 8 px
      // no delay on opening
      transition-delay: 0s; } }

  // animation for the links as the slider opens
  @keyframes navLinksFade {
    // the timings for this are set in mobile-nav-slider.js
    from {
      opacity: 0.1;
      transform: translateX(200px); }
    to {
      opacity: 1;
      transform: translateX(0); } } }
